document.addEventListener("DOMContentLoaded", () => {
  console.log("üî• JS loaded");

  /* ========= modal ========= */
  const overlay = document.getElementById("overlay");
  const modal = document.getElementById("modal--first");
  const closeBtn = modal.querySelector(".close");

  function openModal() {
    overlay.classList.add("show");
    modal.classList.add("show");
  }

  function closeModal() {
    overlay.classList.remove("show");
    modal.classList.remove("show");
    resetCards();
  }

  overlay.addEventListener("click", closeModal);
  closeBtn.addEventListener("click", closeModal);

  // ‚Äª Á¢∫Ë™çÁî®ÔºàÊú¨Áï™„Åß„ÅØÂâäÈô§Ôºâ
  openModal();

  /* ========= card ========= */
  const cards = modal.querySelectorAll(".card-wrapper");

  function resetCards() {
    cards.forEach(card => card.classList.remove("active"));
  }

  cards.forEach(card => {
    card.addEventListener("click", e => {
      e.stopPropagation(); // ‚Üê Ë∂ÖÈáçË¶Å
      console.log("‚úÖ card clicked");

      const isActive = card.classList.contains("active");
      resetCards();
      if (!isActive) card.classList.add("active");
    });
  });

});
